import WelcomeScreen from "./welcome"
import coder from "../assets/global.png"



function HomeDetails(props){
  return(
    <div className="v-homepage-details ">
    <div className="page-center">
    <h2 className="v-title-text1">App Development Services</h2>

<p>This mobile application development agency has it all. 
We offer design and application development services for every stage of your mobile project.</p> 
<p>App Design's mobile application development services are client-centric to create a complete and customized solution. Leading digital agency focused on UI / UX design. 
We develop web pages, mobile applications iOS / Android and we produce Virtual Stores.</p>
    </div>


{props.children}

    </div>
  )
}

function AppsIntro(){
  return(
    <div className="v-block-tabs page-center" >
<div className="v-block-tab">
<img className="v-block-icon" src={pwas} />
<div className="v-block-text">
<h2 className="v-title-text2">Hybrid and PWAs</h2>
<p>We build hybrid apps with popular frameworks that satisfies your needs especially when you need mature but simple mobile apps fast and easy.</p>
</div>
</div>

<div className="v-block-tab">
<img className="v-block-icon" src={android} />
<div className="v-block-text">
<h2 className="v-title-text2">Android Apps</h2>
<p>Build seamless native android apps quick with new and trending cross-platform technologies like NativeScript, React Native and Kotlin.</p>
</div>
</div>

<div className="v-block-tab">
<img className="v-block-icon" src={ios} />
<div className="v-block-text">
<h2 className="v-title-text2">IOS Apps</h2>
<p>Get your apps build and deployed to the AppStore with 100% working modern technologies without the need for Swift fast and easy.</p>
</div>
</div>
    </div>
  )
}

function AppDevDetails(){
  return(
    <div className="v-appdev-details page-center">
  <h2 className="v-title-text1">  Mobile and Desktop  application development</h2>

<p>We develop applications with React Native  and electron. We create apps from scratch, using the latest technology in mobile and desktop application development.</p>
<ul>
  <li> <span className="v-index">Ionic Framework:</span> creation of multi-platform applications</li>
  <li><span className="v-index">React Native:</span> quality native multi-platform apps</li>
  <li><span className="v-index">NativeScript:</span> high quality native applications for iPhone and Android</li>
  <li><span className="v-index">Electron:</span> creation of multi-platform desktop applications</li>
  <li><span className="v-index">Python PyQT:</span> creation of quality desktop applications using Python Programming Language</li>
</ul>
    </div>
  )
}

function SampleApps(props){
  return(
<div className="v-app-samples">
  <h1  className="v-title-text3" style={{color:"white !important"}}>Samples of Developed Hybrid Android Apps</h1>
  <div className="v-app-sample-tabs">
  <div className="v-app-sample-tab">
  <img src={cpuid} className="v-app-sample-icon" />
  <h3 className="v-title-text2">Intel CPU ID</h3>
  <p>Hybrid App that gives detailed information of an Intel-based processors upon query (Offline App).</p>
  </div>

  <div className="v-app-sample-tab">
  <img src={sky24} className="v-app-sample-icon" />
  <h3 className="v-title-text2">SKY 24</h3>
  <p>Hybrid Weather App the works by searching your desired location anytime anywhere (Requires Internet).</p>
  </div>

  <div className="v-app-sample-tab">
  <img src={cacus} className="v-app-sample-icon" />
  <h3 className="v-title-text2">Cacus 1.0</h3>
  <p>Hybrid Android Calculator App that performs simple basic calculation operations.</p>
  </div>
</div>
 <h3  className="v-title-text3" style={{color:"white !important"}}>Get them now for free!</h3>
 <button onClick={props.page} className="adwn v-welcome-btn clickable">Download</button>
 <TechSlideShow />
</div>
  

  )
}
function TechSlideShow(){
  return(
    <div className="tech-slideshow">
      <div className="mover-1"></div>
      <div className="mover-2"></div>
      <span className="techCount"><span>Platforms and</span><span>Technologies</span></span>
</div>
  )
}

