function ItSolutions(props){
  return(
    <div className="v-itsupport-details ">
    <div className="page-center">
   
    <h2 className="v-title-text1">IT Support</h2>

<p>With new generation systems popping up every now and then, the world of PC's users is changing rapidly. New systems built with new technologies require professionals to fix the new issues that trouble users.</p> 
<p>New systems means new security,new flaws, new bugs, new faults and new troubles for PC users. Get solution to all your hardware to software based issues with no hassle.</p>

<ul>
  <li> <span className="v-index">Chip Level Laptop Repairs:</span> Advanced modern computer repairs</li>
  <li><span className="v-index">Password Unlock:</span> Unlock Bios and Windows Passwords</li>
  <li><span className="v-index">Mobile Unlock:</span> FRP Unlock for both android and ios devices</li>
  <li><span className="v-index">System Installation:</span> Full system setup and installation for both network and single PC's</li>
  <li><span className="v-index">Troubleshooting Tips:</span> Quick and easy step by step guide to your day to day PC issues.</li>
</ul>
    </div>
    {/* <button onClick={props.page} className="dmsm v-welcome-btn clickable">Learn More</button> */}
    
    </div>
  )
}
export default function HomePage(props){
return(
  <div>
<WelcomeScreen quote={props.getQuote} icon={coder}  title="Modern IT Solutions" text="We've got you covered when it comes to your IT needs for both your business and personal endeavors. Web, Mobile and Desktop Application Development one stop shop you have been waiting for is here including modern computer hardware solution and much more..." />

<HomeDetails>
<AppsIntro/>
<AppDevDetails />
<SampleApps page={props.getPage}/>
<PlatformsAndTech page={props.getPage}/>
<ItSolutions />
</HomeDetails>

</div>

)

}